import { Component, OnInit } from '@angular/core';
import { Kelompok_komponen } from 'src/app/sdk/payroll/models';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { PayrollKelompokKomponenService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private payrollKelompokKomponenService: PayrollKelompokKomponenService,
        private spinner: NgxSpinnerService,
        private router: Router
    ) { }

    data: Kelompok_komponen = {};
    ngOnInit() {
        this.form();
    }




    dataForm: FormGroupTypeSafe<Kelompok_komponen>;
    form() {
        this.dataForm = this.fb.group<Kelompok_komponen>(
            {
                nama_kelompok: new FormControl('', [Validators.required]),
                jenis: new FormControl('', [Validators.required]),
                sifat: new FormControl(''),
                kode_kelompok: new FormControl('')

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.payrollKelompokKomponenService.postPayrollKelompokKomponens(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/komponen/list'])
    }

}
